.prediction {
    margin-top: 20px;
    height: 100%;
    overflow: hidden;
}

.nextPredict {
    margin-left: 3.5%;
}

.header {
    font-family: 'Comfortaa', cursive;
    color: blueviolet
}

.disclaimer {
    font-family: 'Comfortaa';
    font-size: 80%;
}

.teamsPrediction {
    margin-left: 40px;
}

.custom {
    margin-left: 3%;
}

.teamBox {
    padding: 10px 80px 10px 80px;
    border-radius: 3px
}

.teamBox:focus {
    outline: none;
}

.download-btn {
    width: 250px;
    background-color: aliceblue;
    height: 50px;
    color: midnightblue;
    border: #5e37df;
    box-shadow: 4px 5px 10px 1px #b8b4b8;
    border-radius: 10px;
    margin: 1%
}

.download-btn:hover {
    box-shadow: 4px 10px 10px -2px #b8b4b8;
}

.download-btn:focus {
    outline: none
}

.download-btn:active {
    box-shadow: 0px 0px 0px 0px #b8b4b8;
}

.finalists {
    display: inline-block;
    margin: 1% 5%;
    padding: 10px 25px 10px 25px;
    border-radius: 10px;
    background-color: white;
    border-color: #5e37df;
    color: #5e37df;
    box-shadow: 1px 5px 10px -1px #b8b4b8;
}

.finalists:focus {
    outline: none;
}
