.dataset {
    margin-top: 20px;
    height: 100%;
    overflow: hidden;
}

.analytics{
    margin-left: 3%;
}

.seasonwise {
    margin-left: 2%;
    text-align: center;
}

.stats {
    margin-left: 40px;
    align-items: center;
}

.getit {
    margin-left: 40px;
}

.card {
    display: flex;
    float: left;
    margin: 10px 10px 10px 10px;
    justify-items: center;
    box-shadow: 0px 0px 0px 0.1px #7a68b3;
}

.download-btn {
    width: 250px;
    background-color: aliceblue;
    height: 50px;
    color: #f47216e6;
    border: #5e37df;
    box-shadow: 4px 5px 10px 1px #b8b4b8;
    border-radius: 10px;
}

.download-btn:hover {
    box-shadow: 0px 0px 0px 1px #b8b4b8;
}

.download-btn:focus {
    outline: none
}
