.navContainer {
    display: flex;
    /* align-items: center; */
    justify-content: center;
    margin-top: 10%;
}

.subnavButton {
    width: 250px;
    padding: 10px 50px;
    margin: 15px 50px 15px 50px;
    background-color: white;
    border-radius: 10px;
    border-color: #8966f6;
    color: #8966f6;
    font-weight: 500;
    box-shadow: 4px 5px 10px 1px #b8b4b8;
}

.subnavButton:hover {
    /* background-color: #e6c149; */
    color: goldenrod;
    border-color: gold;
    box-shadow: 4px 5px 10px 1px white;
}

.subnav-active {
    /* background-color: #8966f6;
    color: white;
    border-color: white;
    box-shadow: 4px 5px 10px 1px white; */
    color: #8966f6;
    border-color: gold;
    box-shadow: 4px 5px 10px 1px white;
}

.subnavButton:focus {
    outline: none;
}

.hidden {
    display: none;
}