.navbar {
    background: white;
    padding-top: 10px;
    padding-bottom: 10px;
    align-content: center;
    box-shadow: 0px 10px 5px -1px #b8b4b8;
}

.navbar-brand {
    font-family: 'Comfortaa', cursive;
    color: #8966f6;
}

.navbar-brand :hover {
    font-family: 'Comfortaa', cursive;
    color: #7048eb;
}

.navbar-head {
    text-shadow: 0px 6px 5px #b8b4b8;
    color: #8966f6;
}

sub { 
    font-size: 0.5em;
}

sub :hover { 
    font-size: 0.5em;
}

.nav-menu {
     align-items: center;
     text-align: center;
     margin: 0 auto;
 }

.navButton {
    padding: 10px 50px;
    margin: 0px 10px 0px 10px;
    background-color: white;
    border-radius: 10px;
    border-color: #8966f6;
    color: #8966f6;
    font-weight: 500;
    box-shadow: 4px 5px 10px 1px #b8b4b8;
}

.navButton:hover {
    background-color: #8966f6;
    color: white;
    box-shadow: 4px 5px 10px 1px white;
}

.active {
    background-color: #8966f6;
    color: white;
    box-shadow: 4px 5px 10px 1px white;
}

.navButton:focus {
    outline: none;
}

.userProfile {
    align-content: center;
}

.center {
    line-height: 20px;
    margin-top: 30px;
    text-align: center;
    font-family: 'Comfortaa', cursive;
    font-size: 15px;
    font-weight: bold;
    color: #8966f6;
    text-shadow: 0px 5px 5px #b8b4b8;
}

#propic {
    width: 80px;
    border-radius: 50%;
    border-color: #8966f6;
    box-shadow: 4px 5px 10px 2px #b8b4b8;
}