.models {
    margin-top: 20px;
    height: 100%;
    overflow: hidden;
}

.custom {
    display: inline-block;
    text-align: center;
    align-items: center;
    align-content: center;
    margin: 0 auto;
}

.center {
    text-align: center;
    align-items: center;
    align-content: center;
    margin: 0 auto;
}

.modelEfficiency {
    margin-left: 2%
}

.header {
    font-family: 'Comfortaa', cursive;
    color: blueviolet
}

.disclaimer {
    margin: 1% 4%;
    font-family: 'Comfortaa';
    font-size: 90%;
}

.effStats {
    display: inline-block;
    width: 75%;
    margin: 1.25% 5%;
    padding: 10px 25px 10px 25px;
    border-radius: 10px;
    background-color: white;
    border-color: #5e37df;
    color: #5e37df;
    box-shadow: 1px 5px 10px -1px #b8b4b8;
}

.effStats:focus {
    outline: none;
}

.custTrain {
    width: 60%;
    height: 40px;
    margin: 1% 5%;
    border-color: #5e37df;
    text-align: center;
    border-radius: 10px;
    color: #5e37df;
}

.custTrain:focus {
    outline: none;
}

.download-btn {
    width: 250px;
    background-color: aliceblue;
    height: 50px;
    color: #5e37df;
    border: #5e37df;
    box-shadow: 4px 5px 10px 1px #b8b4b8;
    border-radius: 10px;
    margin: 1%
}

.download-btn:hover {
    box-shadow: 4px 10px 10px -2px #b8b4b8;
}

.download-btn:focus {
    outline: none
}

.download-btn:active {
    box-shadow: 0px 0px 0px 0px #b8b4b8;
}
